$blue_text: #394758;



.table-striped, #table-striped-inside table{
    font-size:12px;
    color:$blue_text !important;
    width:100%;

    caption{
        font-size:24px !important;
        text-align:left;
        text-transform:uppercase;
    }

    tr.header{
        background-color:$head_blue !important;
        th{
            color:#fff;
            text-align: left;
            padding:15px 0 15px 10px;
        }
    }
    tr.striped{
        background-color:$silver_gray;

    }
    tr{
        text-transform: uppercase;
        td,th{
            padding:15px 0 15px 10px;
            text-align: left;
            font-weight: bold;
        }
        th{
            background-color:#808080;
            color:#fff;
        }
        
    }
}

@media (max-width:$screen-xxxs-max){
   .table-striped, #table-striped-inside table{
       tr{
           td{
               font-size:12px;
               span{
                   font-size:12px;
                   strong{
                       font-size:12px;
                   }
               }
           }
       }
   } 
}

