/* Color */
$silver_gray: #eff0f2;
$text_gray:#999999;
$nav_blue:#000;
$nav_blue_transparent:rgba(0, 0, 0, 0.6);
$head_blue:#000;
$title_blue:#1e3044;
$green:#92d400;
$content_gray:#696969;
$gray_background: #f5f5f5;
$link_hover_color:rgba(0,0,0,0.6);
/* Break Points */

$screen-xxxs-min: 320px;
$screen-xxxxs-max: ($screen-xxxs-min - 1);
$screen-xxs-min: 380px;
$screen-xxxs-max: ($screen-xxs-min - 1);
$screen-xs-min: 520px;
$screen-xxs-max: ($screen-xs-min - 1);
$screen-sm-min: 768px;
$screen-xs-max: ($screen-sm-min - 1);
$screen-md-min: 992px;
$screen-sm-max: ($screen-md-min - 1);
$screen-lg-min: 1200px;
$screen-md-max: ($screen-lg-min - 1);
$screen-lg-m-min:1600px;
$screen-lg-max: ($screen-lg-m-min - 1);

/* This section needs to be finished */
/*
$font-fu-l: "futura_lightregular"; 
