$font-fu-l: "proxima-nova"; 
$font-fu-book: "proxima-nova";
$font-fu-bold: "proxima-nova";

/* Mobile box edge */
$mobile_edge:0;
$top_nav_height : 33px;
$mobile-menu-width:200px;

/* Cross browser */
@mixin transition($transition_string){
    -webkit-transition: $transition_string;
    -moz-transition: $transition_string;
    -o-transition: $transition_string;
    transition: $transition_string;
}
//@include transition(all 0.3s ease);

@mixin text_shadow($param1,$param2,$color){
    text-shadow: $param1 $param2 1px $color;
    -webkit-shadow: $param1 $param2 1px  $color;
    -o-shadow: $param1 $param2 1px $color;
    -moz-shadow: $param1 $param2 1px $color;
}

/***Global effects*/
.transparent-bg{
    background-color:rgba(0, 0, 0, 0.4);
    &-darker{
        background-color:rgba(0, 0, 0, 0.6);
    }
}
a{cursor: pointer;
    color:#000;
    &:hover{
        color:$link_hover_color;
        text-decoration:none;
    }
}
//eg.@include transition(all 0.4s ease);
.light-gray{background-color:$silver_gray}
.black{background-color:#000;}
.uppercase{text-transform: uppercase;}

/***Position*/
.text-center{text-align: center;}
.inline-block{display:inline-block;}
.absolute-position{position:absolute;}
.absolute-left{left:0}
.absolute-right{right:0}

.full-height{height:100%}
.full-width{width:100%}
.half-width{width:50%}
.center{margin:0 auto}
/* Button */
.button {
    padding: 12px 30px 12px 30px;
    border: 2px solid #fff;
    @include transition(all 0.3s ease);
    text-transform:uppercase;
    &:hover{
        text-decoration: none;
        box-shadow: inset 0 0 0 2px white;
    }
}

.button-white {
    border: 2px solid #fff;
    color: #fff !important;
}

.button-black {
    border: 2px solid #000;
    color: #000 !important;
    &:hover{
        text-decoration: none;
        box-shadow: inset 0 0 0 2px #000;
    }
}

.transit{
    @include transition(all 1s ease);
}

.transit.move-left{
    margin-left:200px;
    margin-right:-200px;
}
.transit.move-left-in-lg{
    padding-left:200px;
}
.transit.move-left-in-lg .row{
    margin-right:-200px; 
}

body
{
    font-family: $font-fu-l;
    font-size:15px;
    overflow-y: scroll !important;
    overflow-x: hidden !important;
    @include transition(all 0.4s ease);
    padding: 0 30px !important;
}

@media(max-width:$screen-md-max){
    body
    { 
        padding: 0 !important;
    }
}

p{
    font-weight:100;
}
h2.large{
    font-size:30px !important;
    margin-bottom:30px;
}
div,span,form{
    font-weight:300;
}
/* End of global */
