.contact-form{
    padding:40px 0 60px 0;
    position:relative;
}

.contact-form-area{
        position:relative;
        padding-top:50px;
        
        form{
            div.cs-select{
                z-index:97;
            }

            .field-group{
            position:relative;
            input.long, textarea.long{
                width:100%;
                padding:1em;
            }
            textarea.long{
                height:100px;
            }

            label{
                width:100%;
            }

            .cs-select{
                width:100%;
                border:1px solid rgb(169, 169, 169);
                max-width:none;
                padding:1px;
                .cs-options{
                    border:1px solid rgb(169, 169, 169);
                    li:hover{
                        background-color:rgb(169, 169, 169);
                    }
                }
            }

            .error-msg{
                    padding:10px 10px 6px 10px;
                    width:100% -30px;
                    display:inline-block;
                    background-color:#a60000;
                    position:absolute;
                    text-align:left;
                    top:82px;
                    right:15px;
                    left:15px;
                    height:40px;
                    z-index:10;
                    color:#fff;
                }
            }
        input[type=submit]{
            background-color:#000;
            color:#fff;
            font-weight:bold;
            border:none;
            font-family:futura_bold;
            &:hover{
                text-decoration: none;
                background-color:#808080;
                box-shadow: none;
            }
        }

        
    }
    .row{
        margin-bottom:15px;
    }
}

#contact_us_form_result{
    padding:8px 15px 10px 15px;
    font-size:20px; 
    display:inline-block;
    z-index:10;
    position:absolute;
    margin:10px auto;
    width:100%;
    top:-13px;
    text-align:left;
    height:50px;
    color:#fff;
    display:block;
    background-color:red;
    &.green{
        background-color:#92d400;
    }
}



@import 'media_queries/__contact.scss';
